.person {
  h2 {
    @extend h3;

    margin: 0;
  }

  .title {
    color: $color-slate;
  }

  .social-icons {
    li {
      list-style: none;
      margin: 0;
      padding: 0;
      display: inline-block;

      &::before {
        display: none;
      }
    }
  }
}



